@import "demo/assets/styles/variables.scss";




























.main-cnt {
  padding-top: 80px;
  width: $app-main-width;
  height: 100%;
  margin: 0 auto;
  .page-container {
    box-sizing: border-box;
    margin-left: 270px;
    background: #fff;
  }
}
.ivu-back-top {
  border-radius: 50%;
  overflow: hidden;
  border: 1px solid #eee;
  box-shadow: 0 0px 8px 0px rgba(0,0,0,.1);
  .ivu-back-top-inner {
    background: none;
    border-radius: 0;
    box-shadow: none;
    border: none;
  }
  i.ivu-icon{
    color: #2d8cf0;
    font-size: 24px;
    padding: 10px;
    background: #fff;
    border-radius: 50%;
    padding: 10px;
  }
}
